<template>
  <v-dialog v-model="dialog" max-width="290">
    <template v-slot:activator="{ on, attrs }">
      <v-icon color="primary" v-bind="attrs" v-on="on" small>
        mdi-delete
      </v-icon>
    </template>
    <v-card>
      <v-card-title class="text-h5"> Delete Item </v-card-title>

      <v-card-text>
        do you wont delete {{ post.title[lang] }} ??
      </v-card-text>

      <v-card-actions>
        <v-spacer></v-spacer>

        <v-btn color="green darken-1" text @click="dialog = false">
          Cancel
        </v-btn>
        <v-btn color="green darken-1" text @click="deleteItemConfirm">
          <span v-if="!progressDeleteItem">Delete</span>
          <v-progress-circular
            v-if="progressDeleteItem"
            color="primary"
            indeterminate
          ></v-progress-circular>
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { postController } from "./post";
export default {
  props: ["item"],
  computed: {
    lang() {
      return this.$store.getters.lang;
    },
  },
  data() {
    return {
      progressDeleteItem: false,
      post: this.item,
      dialog: false,
    };
  },
  methods: {
    deleteItemConfirm: async function () {
      this.progressDeleteItem = true;
      var state = await postController.deleteItem(this.post.id);
      this.progressDeleteItem = false;
      if (state) {
        this.dialog = false;
        this.$emit("getVal");
      }
    },
  },
};
</script>

